import SingletonMgr from "./SingletonMgr";
import { EventTarget } from 'cc';
 
export default class EventManager extends SingletonMgr {
  public static event: EventTarget = null;
 
  constructor() {
    super();
    this.initialize();
  }
 
  private initialize(): void {
    EventManager.event = new EventTarget();
  }
 
 

  static emit(key: string, data?: any) {
    //cc.game.emit(key, data);
    EventManager.event.emit(key, data)
  }
 
  static on(key: string, callback: any, target?: any) {
    //cc.game.on(key, callback, target);
    EventManager.event.on(key, callback, target);
  }
 
  static off(key: string, callback?: any, target?: any) {
    //cc.game.off(key, callback, target);
    EventManager.off(key, callback, target);
  }
}